import * as React from "react";

// 1. import `ChakraProvider` component
import { ChakraProvider } from "@chakra-ui/react";
import Main from "./Main";

function App() {
  return (
    <ChakraProvider>
      <React.Suspense fallback={<div>Loading...</div>}>
        <Main />
      </React.Suspense>
    </ChakraProvider>
  );
}

export default App;
