import { ModalHeader, ModalBody, Image, Stack } from "@chakra-ui/react";
import React from "react";
import math1 from "./math1.png";
import math3 from "./math3.png";
import info1 from "./info1.png";
import chem from "./chem.png";
import bio from "./bio.png";
import phy from "./phy.png";
import earth3 from "./earth3.png";
import earth4 from "./earth4.png";

function Content(props) {
  switch (props.area) {
    case "math":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Stack>
              <Image src={math1} alt="1 " />
              <Image src={math3} alt="3 " />
            </Stack>
          </ModalBody>
        </div>
      );
    case "it":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Image src={info1} alt="1  + " />
          </ModalBody>
        </div>
      );
    case "phy":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Image src={phy} alt=" 3  " />
          </ModalBody>
        </div>
      );
    case "chem":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Image src={chem} alt=" 2  " />
          </ModalBody>
        </div>
      );
    case "bio":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Image src={bio} alt=" 2  " />
          </ModalBody>
        </div>
      );
    case "earth":
      return (
        <div>
          <ModalHeader> </ModalHeader>
          <ModalBody>
            <Stack>
              <Image src={earth3} alt=" 3  " />
              <Image src={earth4} alt="" />
            </Stack>
          </ModalBody>
        </div>
      );
    default:
      return (
        <div>
          <ModalHeader>,   </ModalHeader>
          <ModalBody>error</ModalBody>
        </div>
      );
  }
}

export default Content;
